import SpriteSheet from './SpriteSheet.js';
import home from './sprites/map/home.json'
import homeObj from'./sprites/map/home-obj.json';
import red from'./sprites/character/red.json';
import placeHomeObj from './placeObjects/homeObj.json';
import {createAnim} from './anim.js';
import heroImage from './img/hero.png';
import tilesImage from './img/tiles.png';

export function loadImage(name){
    console.log('@@@', name);
    let url = null;
    switch (name) {
        case 'hero':   url = heroImage; break;
        case 'tiles':  url = tilesImage; break;
        default:
            break;
    }
    //context에 넣을 이미지 요소 생성
    return new Promise(resolve => {
        const image = new Image();
        image.addEventListener('load', () =>{
            resolve(image)
        });
        image.src = url;
    });
}

export function loadJSON(name){
    //json파일 불러오기
    switch (name) {
        case 'home':     return home;
        case 'homeObj' : return homeObj;
        case 'red' :     return red;
        case 'placeHomeObj' : return placeHomeObj;
        default:
            break;
    }
}

