export function loadSpriteSheet(name) {
    return loadImage
    .then(async image => {
        const sheetSpec = await loadJSON(name);
        const sprites = new SpriteSheet(
            image, sheetSpec.tileW, sheetSpec.tileH , sheetSpec.sw, sheetSpec.sh);

        if (sheetSpec.tiles) {
            sheetSpec.tiles.forEach(tileSpec => {
                sprites.defineTile(
                    tileSpec.name,
                    tileSpec.index[0],
                    tileSpec.index[1]);
            });
        }

        if (sheetSpec.frames) {
            sheetSpec.frames.forEach(frameSpec => {
                sprites.define(frameSpec.name, ...frameSpec.rect);
            });
        }

        if (sheetSpec.animations) {
            sheetSpec.animations.forEach(animSpec => {
                const animation = createAnim(animSpec.frames, animSpec.frameLen);
                sprites.defineAnim(animSpec.name, animation);
            });
        }

        return sprites;
    });
}

export function loadBackgroundSprites(images, json){
    //맵 오브젝트 지정
    return loadImage(images)
    .then(async image => {
        const obj = await loadJSON(json);
        const tileW = obj.tileW || 16;
        const tileH = obj.tileH || 16; 
        const sprites = new SpriteSheet(image, tileW, tileH);
        obj.tiles.forEach(({name,index}) => {
            sprites.defineTile(name,index[0],index[1]);
        });
        return sprites;
    })
}

