import Entity from './Entity.js';
import {loadRedSprites} from './loader.js';
import Go from './traits/Go';
import hero from './img/hero.png';
import {createAnim} from './anim.js';

export function createRed(){
    return loadRedSprites('hero')
    .then(sprite => {
        const red = new Entity();
        red.pos.set(16 * 5, 16 * 6);
        // red.vel.set(200, -600);
        red.addTrait(new Go());

        const runAnim = createAnim(['red-right-0', 'red-right-1', 'red-right-2'], 10);
        function routeFrame(red) {
            if (red.go.dir !== 0) {
                return runAnim(red.go.distance);
            }
            return 'red-idle';
        }
        //캔버스에 그릴 위치
        red.draw = function drawRed(context) {
            // sprite.draw('red-idle', context, this.pos.x, this.pos.y);
            // sprite.draw('red-idle', context, this.pos.x, this.pos.y);
            sprite.draw(routeFrame(this), context, this.pos.x, this.pos.y);
        }

        red.update = function updateRed(deltaTime){
            this.pos.x += this.vel.x * deltaTime;
            this.pos.y += this.vel.y * deltaTime;
        }
        
        return red;
    });

}