import Keyboard from './KeyboardState.js';

export function setupKeyboard(entity) {
    console.log('> setupKeyboard');
    const input = new Keyboard();
    input.addMapping('Space', keyState => {
        
        try{
            if (keyState) {
                entity.jump.start();
            } else {
                entity.jump.cancel();
            }
        }catch(e){
            alert('현재 개발 중입니다.');
        }
    });

    input.addMapping('ArrowRight', async keyState => {
        try{
             if(keyState) await entity.go.moveRight();
        }catch(e){
            alert('현재 개발 중입니다.');
        }
    });

    input.addMapping('ArrowLeft', async keyState => {
        try{
            if(keyState) await entity.go.moveLeft();
        }catch(e){
            alert('현재 개발 중입니다.');
        }
    });
    return input;
}
