import React, {PureComponent} from 'react';
import { connect } from 'react-redux';
import './pokemon-00.scss';
import Exe from './main.js';

class Pockemon00 extends PureComponent {
    constructor(props){
        console.log('>Pockemon00');
        super(props);
    }
    componentDidMount(){
        const exe = new Exe();
        exe.exe();
    }

    render(){
        return (
            <canvas id="screen" width="200" height="200"/>
        )
    }
}

function mapStateToProps(state) {
    return state.redux_commonInfo;
}

export default connect(mapStateToProps, null)(Pockemon00);