export default class SpriteSheet {
    constructor(image, width, height, sw, sh){
        this.image = image;
        this.width = width;
        this.height = height;
        this.sw = sw;
        this.sh = sh;
        this.tiles = new Map();
    }

    define(name, x, y, width, height ,sw, sh){
        const buffer = document.createElement('canvas');
        buffer.width =  width
        buffer.height = height;
        buffer
            .getContext('2d')
            .drawImage(
                this.image,
                x,
                y,
                width,
                height,
                0,
                0,
                sw || width,
                sh || height
            );
        this.tiles.set(name, buffer);
    }

    defineTile(name, x, y , w, h, sw, sh) {
        const width = w || this.width;
        const height = h || this.height;
        const sW = sw || this.sw || width;
        const sH = sh || this.sh || height;

        this.define(
            name, 
            x * width, 
            y * height, 
            width, 
            height, 
            sW, 
            sH
        );
    }

    draw(name, context, x, y) {
        const buffer = this.tiles.get(name);
        context.drawImage(buffer, x, y);
    }

    drawTile(name, context, x, y) {
        this.draw(name, context, x * this.width, y * this.width);
    }
}