    constructor(){
        const canvas = document.getElementById('screen');
        this.context = canvas.getContext('2d');
        this.context.imagesSmoothingEnabled = false;
        //전체 배경
        this.context.fillRect(0,0,16 * 12, 16 * 12);
    }
    
